import React, {useState} from 'react';
import Popup from 'reactjs-popup';
import 'reactjs-popup/dist/index.css';

import "./MenuCheckout.css";
// This is a functional component that represents a single menu item. It currently takes in the title and displays it in an h2 element.
// Modify the component to take in all the other properties of a menu item you need and display them in the component.
// Use bootstrap to style the elements so that it looks like the mockup in the assignment.
// Hint: You can use the image name to get the image from the images folder.
const MenuItem = ({ menuItems, clearAll, cart }) => {
    
    const clear = () => {
        clearAll();
    }

    function getText() {
        let res = ""
        for (let i = 0; i < cart.length - 1; i++) {
            if (cart[i] > 0) {
                res += cart[i].toString() + " " + menuItems[i].title + "  "
            }
        }
        if (res.length == 0) {
            res = "No items contained in order"
        }
        return res
    }
    
    let total_ind = cart.length;

    return (    
        <div className="footer-box row">
            <p className="total"> Subtotal: ${Math.round(cart[total_ind - 1] * 100) / 100} </p>
            <Popup className="popup" trigger={<button className="checkout-button" onClick={() => clear()}>Order</button>} modal nested>
                {close => (
                        <div>
                            <p className="order-text">Order placed!</p>
                            <p>{getText()}</p>
                            <div>
                                <button className="checkout-button" onClick=
                                    {() => close()}>
                                        OK
                                </button>
                            </div>
                        </div>
                )}
            </Popup>
            <button className="checkout-button" onClick={() => clear()}>Clear all</button>
        </div>
    );
};

export default MenuItem;
