import './App.css';
import MenuItem from './components/MenuItem';
import MenuHeader from './components/MenuHeader';
import MenuCheckout from './components/MenuCheckout';
import React, { useState } from 'react';

// import 'bootstrap/dist/css/bootstrap.min.css'; // This imports bootstrap css styles. You can use bootstrap or your own classes by using the className attribute in your elements.

// Menu data. An array of objects where each object represents a menu item. Each menu item has an id, title, description, image name, and price.
// You can use the image name to get the image from the images folder.
const menuItems = [
  {
    id: 1,
    title: 'Gyoza',
    description: 'Japanese dumplings',
    imageName: 'gyoza.png',
    price: 5.99,
  },
  {
    id: 2,
    title: 'Sushi',
    description: 'Japanese rice rolls',
    imageName: 'sushi.png',
    price: 6.99,
  },
  {
    id: 3,
    title: 'Ramen',
    description: 'Japanese noodle soup',
    imageName: 'ramen.png',
    price: 7.99,
  },
  {
    id: 4,
    title: 'Matcha Cake',
    description: 'Japanese green tea cake',
    imageName: 'matcha-cake.png',
    price: 4.99,
  },
  {
    id: 5,
    title: 'Mochi',
    description: 'Japanese rice cake',
    imageName: 'mochi.png',
    price: 3.99,
  },
  {
    id: 6,
    title: 'Yakitori',
    description: 'Japanese skewered chicken',
    imageName: 'yakitori.png',
    price: 2.99,
  },
  {
    id: 7,
    title: 'Takoyaki',
    description: 'Japanese octopus balls',
    imageName: 'takoyaki.png',
    price: 5.99,
  },
  {
    id: 8,
    title: 'Sashimi',
    description: 'Japanese raw fish',
    imageName: 'sashimi.png',
    price: 8.99,
  },
  {
    id: 9,
    title: 'Okonomiyaki',
    description: 'Japanese savory pancake',
    imageName: 'okonomiyaki.png',
    price: 6.99,
  },
  {
    id: 10,
    title: 'Katsu Curry',
    description: 'Japanese curry with fried pork',
    imageName: 'katsu-curry.png',
    price: 9.99,
  }
];


function App() {
  let arr = [];
  for (let i = 0; i < Object.keys(menuItems).length + 1; i++) {
    arr.push(0);
  }
  const [cart, setCart] = useState(arr);

  const addItem = (item) => {
    setCart(old_cart => {
      const new_cart = [...old_cart];
      new_cart[item - 1] += 1;
      new_cart[new_cart.length - 1] += menuItems[item - 1].price;
      console.log(new_cart);
      return new_cart;
      });
  }

  const subtractItem = (item) => {
    setCart(old_cart => {
      const new_cart = [...old_cart];
      new_cart[item - 1] -= 1;
      new_cart[new_cart.length - 1] -= menuItems[item - 1].price;
      console.log(new_cart);
      return new_cart;
      });
  }

  const clearAll = () => {
    setCart(old_cart => {
      const new_cart = [...old_cart];
      for (let i = 0; i < new_cart.length; i++) {
        new_cart[i] = 0;
      }
      console.log(new_cart);
      return new_cart;
      });
  }

  let menuComponents = [];
  menuItems.forEach((data) => {
    cart[data.title] = 0;
    menuComponents.push(<MenuItem id={data.id} title={data.title} desc={data.description} 
      image={data.imageName} price={data.price} addItem={addItem} 
      subtractItem={subtractItem} cart={cart}></MenuItem>)
  })
  return (
    <div className="parent">
      <div>
        <div>{<MenuHeader></MenuHeader>}</div>
        <div>{menuComponents}</div>
        <div className='checkout'>{<MenuCheckout menuItems={menuItems} clearAll={clearAll} cart={cart}></MenuCheckout>}</div>
      </div>
    </div>
  );
}

export default App;
