import React, {useState} from 'react';

import "./MenuItem.css";
// This is a functional component that represents a single menu item. It currently takes in the title and displays it in an h2 element.
// Modify the component to take in all the other properties of a menu item you need and display them in the component.
// Use bootstrap to style the elements so that it looks like the mockup in the assignment.
// Hint: You can use the image name to get the image from the images folder.
const MenuItem = ({ id, title, desc, image, price, addItem, subtractItem, cart}) => {
    const [, forceUpdate] = useState();

    const add = (item) => {
        addItem(item);
        forceUpdate(Math.random());
    }

    const sub = (item) => {
        if (cart[item - 1] > 0) {
            subtractItem(item);
        }
    }
    
    return (    
        <div className="menu-parent">
            <div className="info row">
                <div className="col">
                    <img className="image" src={require("../images/" + image)} alt={desc}></img>
                </div>
                <div className="col">
                    <div className="item">
                        <div>
                            <p className="item-name">{title}</p>
                            <p className="item-desc">{desc}</p>
                        </div>
                    </div>
                    <div className="pricing-items row">
                        <div className="col"> 
                            <div className="price">
                                <p className="price-text">${price}</p>
                            </div>
                        </div>
                        <div className='col'>
                            <div className='add'>
                                <div className="row">
                                    <div className="col">
                                        <button className="state-button" onClick={() => sub(id)}>-</button>
                                    </div>
                                    <div className="col count">
                                        <p> {cart[id - 1]} </p>
                                    </div>
                                    <div className="col">
                                        <button className="state-button" onClick={() => add(id)}>+</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
               
            </div>
        </div>
    );
};

export default MenuItem;
